.participant {
    /* min-height: 100vh; */
    /*height: calc(100vh - 100px);*/
    height: auto;
    display: flex;
    flex: 1 0 auto;
    justify-content: center;
    align-items: center;
    /* z-index: 100; */
}

.container {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 15px;
}

.participant__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    grid-gap: 20px;
}

.participant__links {
    display: flex;
}

.participant__link {
    margin-right: 10px;

    font-size: 30px;
    color: #000;
}

.participant__link:hover {
    color: #4facfe;
}
.participant__contacts {
    display: flex;
    flex-direction: column;
    max-width: 480px;
    max-height: 560px;
    justify-self: center;
    align-items: center;
}
.participant__box {
    max-height: 480px;
    overflow: hidden;
    border: 10px solid #fff;
    color: white;
    animation: ava-animate 5s infinite alternate;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.5);
}

.contacts__icons {
    z-index: 500;
    width: 300px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.participant__box-img {
    animation: ava-bg 3s infinite alternate;
    margin-bottom: 20px;
}

@media screen and (max-width: 1028px) {
    .participant {
        padding-top: 30px;
    }

    .participant__container {
        grid-row-gap: 20px;
    }

    .participant__about {
        text-align: center;
    }

    .participant__links {
        justify-content: center;
    }

    .participant__box {
        max-width: 300px;
        max-height: 300px;
        justify-self: center;
    }
}

@keyframes ava-animate {
    0% {
        border-radius: 62% 38% 72% 28% / 67% 32% 68% 33%;
    }

    40% {
        border-radius: 37% 63% 53% 47% / 67% 32% 68% 33%;
    }

    60% {
        border-radius: 23% 77% 39% 61% / 78% 54% 46% 22%;
    }

    100% {
        border-radius: 18% 82% 24% 76% / 43% 54% 46% 57%;
    }
}

@keyframes ava-bg {
    from {
        backdrop-filter: hue-rotate(0);
    }

    to {
        backdrop-filter: hue-rotate(90deg);
    }
}

.contacts {
    height: 20px;
    color: white;
    font-size: 20px;
}
